@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
.map {
	overflow: hidden;
	height: 500px;
	width: 100%;

	background-color: white;

	padding: 1rem;
	margin-top: 16px;

	border-radius: 20px;
	box-shadow: -1px -1px 68px -9px rgba(255, 255, 255, 1);
}

@media screen and (max-width: 768px) {
	.map {
		height: 400px;
		width: 100vw;
	}
}

.map .leaflet-container {
	height: 100%;
}

.info-container {
	width: 150px;
}

.info__logo {
	border-radius: 8px;
}
.info__logo img {
	display: inline-block;
	height: 40px;

	padding-right: 0;
	border: 0;
	vertical-align: middle;
}

.info__name {
	margin-top: 10px;
	font-size: 20px;
	font-weight: bold;
	color: #555;
}

.info__address {
	font-size: 16px;
	margin-top: 5px;
}
.body {
	background-color: #95c2de;
	height: calc(100vh - 80px);
	display: flex;
	justify-content: center;
	align-items: center;
}

.mainbox {
	background-color: #95c2de;
	margin: auto;
	height: 600px;
	width: 600px;
	position: relative;
}

.err {
	color: #ffffff;
	font-family: "Nunito Sans", sans-serif;
	font-size: 11rem;
	position: absolute;
	left: 20%;
	top: 8%;
}

.far {
	position: absolute;
	font-size: 8.5rem;
	left: 42%;
	top: 15%;
	color: #ffffff;
}

.err2 {
	color: #ffffff;
	font-family: "Nunito Sans", sans-serif;
	font-size: 11rem;
	position: absolute;
	left: 68%;
	top: 8%;
}

.msg {
	text-align: center;
	font-family: "consolas", sans-serif;
	font-size: 2rem;
	position: absolute;
	left: 16%;
	top: 45%;
	width: 75%;
}

.a {
	text-decoration: none;
	color: white;
}

.a:hover {
	text-decoration: underline;
}

.blocks {
	height: 100vh;
	display: flex;
	align-items: center;
	position: relative;
	justify-content: center;
}

.block {
	--sz: 8vmin;
	--tX: 0;
	--animation: 700ms cubic-bezier(0.3, 0.5, 0.4, 0.9) infinite alternate-reverse;
	--hm: 4.5vmin;
	height: var(--sz);
	width: var(--sz);
	background-image: var(--bg);
	border-radius: 50%;
	-webkit-transform: translateX(var(--tX));
	        transform: translateX(var(--tX));
	mix-blend-mode: lighten;
}

.orange {
	--bg: linear-gradient(-50deg, #fbab7e 0%, #f7ce68 100%);
	margin-right: var(--hm);
	-webkit-animation: attract-orange var(--animation);
	        animation: attract-orange var(--animation);
}

.blue {
	--bg: linear-gradient(50deg, #eea74a 0%, #c2bbb4 100%);
	margin-left: var(--hm);
	-webkit-animation: attract-blue var(--animation);
	        animation: attract-blue var(--animation);
}

@-webkit-keyframes attract-orange {
	to {
		-webkit-transform: translateX(calc(var(--sz) + calc(var(--hm) / 4)));
		        transform: translateX(calc(var(--sz) + calc(var(--hm) / 4)));
	}
}

@keyframes attract-orange {
	to {
		-webkit-transform: translateX(calc(var(--sz) + calc(var(--hm) / 4)));
		        transform: translateX(calc(var(--sz) + calc(var(--hm) / 4)));
	}
}

@-webkit-keyframes attract-blue {
	to {
		-webkit-transform: translateX(calc(var(--sz) * -1 - calc(var(--hm) / 4)));
		        transform: translateX(calc(var(--sz) * -1 - calc(var(--hm) / 4)));
	}
}

@keyframes attract-blue {
	to {
		-webkit-transform: translateX(calc(var(--sz) * -1 - calc(var(--hm) / 4)));
		        transform: translateX(calc(var(--sz) * -1 - calc(var(--hm) / 4)));
	}
}
.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

